#book {
    margin: 20px;
    overflow: auto;
    clear: both;
    background: #ffffff;
}

#list {
    float: left;
    width: 40%;
}

#details {
    float: left;
    width: 60%;
}

.contact {
    background: #EEE;
    padding: 5px;
    margin: 5px;
    vertical-align: middle;
    cursor: pointer;
}

.contact:hover {
    background: #d59450;
}

.avatar {
    display: inline-block;
    width: 96px;
    height: 96px;
    font-size: 5em;
    line-height: 96px;
    vertical-align: middle;
    text-align: center;
    background: #838383;
    color: #ffffff;
}

.small {
    width: 48px;
    height: 48px;
    font-size: 2.5em;
    line-height: 48px;
}

.title {
    display: inline-block;
    padding: 5px;
    font-weight: bold;
}

.name {
    font-size: 2em;
    font-weight: bold;
    margin: 5px 20px;
}

.info-line {
    margin: 5px;
}

.info {
    overflow: auto;
    padding: 5px;
}

.info span {
    display: block;
}

.col {
    float: left;
}
body {
    margin: 0;
    padding: 0;
    background: #DDDDDD;
    font-family: sans-serif;
}

.container {
    margin: 10px auto 10px auto;
    max-width: 900px;
    min-height: 90vh;
    background: #EEEEEE;
    position: relative;
    padding-bottom: 2em;
}

header {
    background: #d59450;
    color: #fff;
    text-align: left;
    font-size: 2em;
    font-weight: bold;
    padding: 0.5em;
}

footer {
    background: #d59450;
    color: #fff;
    text-align: center;
    padding: 0.5em;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
}

.content {
    margin: 0;
    padding: 1em;
    background: #fff;
}

.centered {
    text-align: center;
    vertical-align: middle;
}

h1 {
    background: #d59450;
    margin: 0;
    padding: 0.25em 1em 0.25em 1em;
    color: #fff
}

.box {
    text-align: left;
    display: inline-block;
    margin: auto;
    margin-top: 20%;
    width: 250px;
}

.box label, .box input {
    box-sizing: border-box;
    display: block;
    width: 90%;
    margin: 0.25em auto 0.25em auto;
}

.box label {
    margin-top: 1em;
}

a {
    text-decoration: none;
}

a:hover {
    color: #d59450;
}
